</template>

<script>
/* eslint-disable global-require */
import { ValidationProvider, ValidationObserver } from 'vee-validate'
import VuexyLogo from '@core/layouts/components/Logo.vue'
import {
  BRow, BCol, BLink, BFormGroup, BFormInput, BInputGroupAppend, BInputGroup, BFormCheckbox, BCardText, BCardTitle, BImg, BForm, BButton,
} from 'bootstrap-vue'
import { required, email } from '@validations'
import { togglePasswordVisibility } from '@core/mixins/ui/forms'
import store from '@/store/index'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
// import axios from '@axios'

export default {
  components: {
    BRow,
    BCol,
    BLink,
    BFormGroup,
    BFormInput,
    BInputGroupAppend,
    BInputGroup,
    BFormCheckbox,
    BCardText,
    BCardTitle,
    BImg,
    BForm,
    BButton,
    VuexyLogo,
    ValidationProvider,
    ValidationObserver,
  },
  mixins: [togglePasswordVisibility],
  data() {
    return {
      status: '',
      password: 'password',
      userEmail: 'admin@unia.edu.pe',
      sideImg: require('@/assets/images/layouts/interculturalidad.png'),
      // validation rulesimport store from '@/store/index'
      required,
      email,
    }
  },
  computed: {
    passwordToggleIcon() {
      return this.passwordFieldType === 'password' ? 'EyeIcon' : 'EyeOffIcon'
    },
    imgUrl() {
      if (store.state.appConfig.layout.skin === 'dark') {
        // eslint-disable-next-line vue/no-side-effects-in-computed-properties
        this.sideImg = require('@/assets/images/pages/login-v2-dark.svg')
        return this.sideImg
      }
      return this.sideImg
    },
  },
  methods: {
    validationForm() {
      /* const url = 'http://192.168.0.6/dibu-api/public/api/login'

      console.log('entre')
      axios.post(url, {
        email: 'bu_rsdep',
        password: 'password',
      }, { withCredentials: true })
        .then(response => {
          console.log('respuesta=', response)
          localStorage.setItem('token', response.data.token)
        })
        .catch(error => {
          console.log(error)
        }) */

      this.$refs.loginValidation.validate().then(success => {
        if (success) {
          if (this.userEmail === 'admin@unia.edu.pe' && this.password === 'password') {
            setTimeout(() => {
              this.$router.push('/')
              localStorage.setItem('usuario', 'Administrador')
              this.$toast({
                component: ToastificationContent,
                props: {
                  title: 'Ingreso exitoso!!',
                  icon: 'EditIcon',
                  variant: 'success',
                },
              })
            }, 2000)
          } else if (this.userEmail === 'cultural@unia.edu.pe' && this.password === 'password') {
            setTimeout(() => {
              this.$router.push('talleres-culturales')
              // localStorage.setItem('userData', JSON.stringify(userData))
              localStorage.setItem('usuario', 'Director de Cultura')
              this.$toast({
                component: ToastificationContent,
                props: {
                  title: 'Ingreso exitoso!!',
                  icon: 'EditIcon',
                  variant: 'success',
                },
              })
            }, 2000)
          } else if (this.userEmail === 'deportivo@unia.edu.pe' && this.password === 'password') {
            setTimeout(() => {
              this.$router.push('talleres-deportivo')
              localStorage.setItem('usuario', 'Director Deportivo')
              this.$toast({
                component: ToastificationContent,
                props: {
                  title: 'Ingreso exitoso!!',
                  icon: 'EditIcon',
                  variant: 'success',
                },
              })
            }, 2000)
          } else {
            this.$toast({
              component: ToastificationContent,
              props: {
                title: 'Credenciales incorrectas!!',
                icon: 'AlertIcon',
                variant: 'danger',
              },
            })
          }
